import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { ITitleSubtitle } from '../../../../types/table-cells';

/**
 * @category Table Cell
 * @description A cell component for displaying a title and subtitle.
 */

@Component({
    tag: 'atui-title-subtitle-cell',
    shadow: false,
})
export class AtuiTitleSubtitleCell implements ICellRendererComp {
    @Element() el;
    @State() value: ITitleSubtitle;
    @State() params: ICellRendererParams;

    init(params: ICellRendererParams): void {
        const { value } = params;

        this.params = params;
        this.value = value;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: ICellRendererParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    private updateCell(newValue: ITitleSubtitle) {
        this.params.value = newValue;
        this.value = newValue;
    }

    render() {
        return (
            <Host>
                <div class="flex h-full flex-col justify-center">
                    <div class="truncate text-sm leading-normal">
                        {this.value?.title}
                    </div>
                    <div class="truncate text-xs font-normal leading-normal text-med">
                        {this.value?.subtitle}
                    </div>
                </div>
            </Host>
        );
    }
}
